export default {
    send: function send(options) {
        fetch(options.url, {
            method: 'post',
            body: JSON.stringify(options.data),
            headers: { 'content-type': 'application/json' }
        }).then(function (res) {
            if (res.ok) {
                return res.json();
            } else {
                throw new Error('Fetch error: '.concat(res.statusText));
            }
        }).then(function (response) {
            var data = response;
            if (!data || data.code !== 0) {
                options.error && options.error(data && data.msg);
                return;
            }
            options.success && options.success(data);
        }).catch(function (e) {
            console.error(e);
            options.error && options.error();
        });
    },
    read: function read(options) {
        fetch(options.url).then(function (res) {
            if (res.ok) {
                return res.json();
            } else {
                throw new Error('Fetch error: '.concat(res.statusText));
            }
        }).then(function (response) {
            var data = response;
            if (!data || data.code !== 0) {
                options.error && options.error(data && data.msg);
                return;
            }
            options.success && options.success(data.data.map(function (item) {
                return {
                    time: item[0],
                    type: item[1],
                    color: item[2],
                    author: item[3],
                    text: item[4]
                };
            }));
        }).catch(function (e) {
            console.error(e);
            options.error && options.error();
        });
    }
};